@import url('https://fonts.googleapis.com/css2?family=Neucha&display=swap');

.App {
  width: 100vw;
  height: 100vh;
  display: flex; /*включаем flrx-box*/
  flex-direction: column;
  align-items: center;
  /* background-color: #2f74c0;d */
  background-image: url("./img/raider.jpg");  
  background-size: 1750px;
  background-position-y: -15px;
  font-family: "Neucha", cursive;
}

.heading {
  text-transform: uppercase;
  font-size: 40px;
  margin: 30px 0;
  color: white;
  z-index: 1;
  text-align: center;
}

@media (max-width: 800px) {
  .heading {
    margin: 15px 0;
    font-size: 35px;
  }
}
