.input {
    display: flex;
    width: 95%;
    position: relative;
    align-items: center;
}

.input__box {
    width: 100%;
    border-radius: 50px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
}

.input__box:focus {
    box-shadow: 0 0 10px 1000px rgba(0, 0, 0, 0.5);
    outline: none;
}

.input_submit {
    position: absolute;  /* помещает submit go в внутрь inputа */
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0px;
    border: none;
    font-size: 25px;
    background-color: #4a584b  ;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 10px black;
}

.input_submit:hover {
    background-color: #2c8a2c;   /* когда кликаю на кнопку go меняется ее цвет на чуть более яркий синий */

}

.input_submit:active{  /* когда кликаю на кнопку go меняется ее размер и тень становится меньше */
    transform: scale(0.8);
    box-shadow: 0 0 5px black;  
}

.container {
  display: flex;
  width: 95%;
  margin-top: 10px;
  justify-content: space-between;
  align-items: flex-start;
}

.todos {
    display: flex;
    width: 47.5%;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 15px;
    border-radius: 5px;
    background-color: #4a584b;
}

.todos__heading {
  font-size: 30px;
  color: white;
}

.todos__single {
    display: flex;
    border-radius: 5px;
    padding: 20px;
    margin-top: 15px;
    background-image: url("/src/img/for_backgorund.jpeg");
    background-size: 600px;
    transition: 0.2s;
}
.todos__single:hover {
  box-shadow: 0 0 5px black;
  transform: scale(1.03);
}

.todos__single--text {
    flex: 1;
    padding: 5px;
    border: none;
    font-size: 20px;
  }
  
  .icon {
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
  }

  @media (max-width: 1100px) {
    .todos {
      width: 45%;
    }
  }

  @media (max-width: 700px) {
    .input {
      width: 95%;
    }
    .todos {
      width: 95%;
      margin-bottom: 10px;
    }
    .container {
      width: 95%;
      flex-direction: column;
    }
  }

.remove {
    background-color: rgb(235, 103, 80);
}

.dragactive {
  background-color: #323e32;
}

.dragcomplete {
  background-color: rgb(255, 38, 0);
}

.drag {
  box-shadow: 0 0 20px black;
}
